@use 'stack';
@use 'typography';


:root {
	@include stack.custom-property();
	@include typography.custom-property();
}

@for $i from 1 through 16 {
	[data-nc-font='#{$i}'] {
		@include typography.font-size-line-height($i);
	}
}
